import {profileAPI} from "./api/api";

const ADD_POST_TO_DATA = 'ADD-POST-TO-DATA';
const UPDATE_POST_TEXT = 'UPDATE-POST-TEXT';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';

export let addPostToDataActionCreator = (newPostValue) => ({type: ADD_POST_TO_DATA, newPostValue})
export let setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export let setStatus = (status) => ({type: SET_STATUS, status})
export let updatePostTextActionCreator = (text) => ({
    type: UPDATE_POST_TEXT,
    newText: text
});

let initialState = {
    myPostsData: [
        {id: 1, message: 'Hey my name is Tolesh', likecount: 15},
        {id: 2, message: 'hey bro', likecount: 23},
    ],
    newPostText: [
        "it-kamasutra"
    ],
    profile: null,
    status: ""

}

const profileReducer = (state = initialState, action) => {
    if (action.type === ADD_POST_TO_DATA) {
        let newPost = {
            id: 5,
            message: action.newPostValue,
            likecount: 0

        };
        let stateCopy = {...state};
        stateCopy.myPostsData = [...state.myPostsData];
        stateCopy.myPostsData.push(newPost);
        //stateCopy.newPostText = '';
        return stateCopy;

    } else if (action.type === UPDATE_POST_TEXT) {
        let stateCopy = {...state};
        stateCopy.myPostsData = [...state.myPostsData];
        stateCopy.newPostText = action.newText;
        return stateCopy;

    } else if (action.type === SET_USER_PROFILE) {
        let stateCopy = {...state, profile: action.profile};

        return stateCopy;
    } else if (action.type === SET_STATUS) {
        let stateCopy = {...state, status: action.status};

        return stateCopy;
    }

    return state;

}

export const getProfile = (userId) =>
    (dispatch) => {
        // if (!userId) {
        //     userId = 12;
        // }
        profileAPI.getProfile(userId).then(data => {
            dispatch(setUserProfile(data));
        });
    }

export const getStatus = (userId) =>
    (dispatch) => {
     profileAPI.getStatus(userId).then(response => {

         dispatch(setStatus(response.data))
     })
    }

export const updateStatus = (status) =>
    (dispatch) => {
        profileAPI.updateStatus(status).then(response => {
            if(response.data.resutCode===0) {

                dispatch(setStatus(status))
            }
        })
    }

export default profileReducer;