import {headersAPI} from "./api/api";
import {stopSubmit} from "redux-form";

export let setAuthUserData = (id,email,login,isAuth) => ({type: 'SET_USER_DATA', data: {id,email,login,isAuth}})
//export let setUserLogin = (login,password) => ({type: 'SET_USER_LOGIN', data: {login,password}})


const SET_USER_DATA = 'SET_USER_DATA';
// const SET_USER_LOGIN = 'SET_USER_LOGIN';


let initialState = {
    id: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false,
    //isRememberMe: false,
    //password: null
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA: {
            return { ...state,
                ...action.data,

                //isRememberMe: true
            }
        }
        // case SET_USER_LOGIN: {
        //     return {
        //         ...state,
        //         ...action.data,
        //         isLogin: true
        //     }
        // }

        default:
            return state;

    }
}

export const getUserAuth = () => (dispatch) => {
   return headersAPI.getAuth().then(data => {

        if (data.resultCode === 0){
            let {id, login, email} = data.data;
            dispatch(setAuthUserData(id,email,login,true));
        }
    });

}

export const getUserLogin = (email,password,rememberMe) => (dispatch) => {



    headersAPI.getLogin(email,password,rememberMe).then(response => {
        if (response.data.resultCode === 0){

            dispatch(getUserAuth());
        } else {
            let message = response.data.messages.length>0 ? response.data.messages[0] : "Some error";
            dispatch(stopSubmit("login", {_error:message}));
        }
    });
}

export const getUserLogout = (email,password,rememberMe) => (dispatch) => {

    headersAPI.getLogout().then(response => {
        if (response.data.resultCode === 0){

            dispatch(setAuthUserData(null,null,null,false));
        }
    });
}

export default authReducer;