export let addMessageActionCreator = (message) =>({
    type: 'ADD-MESSAGE',
    message
})
export let updateMessageActionCreator = (text) => ({
    type: 'UPDATE-MESSAGE',
    newMessage: text
})
const ADD_MESSAGE = 'ADD-MESSAGE';
const UPDATE_MESSAGE = 'UPDATE-MESSAGE';

let initialState = {
    dialogsData: [
        {id: '1', name: 'Tolik'},
        {id: '2', name: 'Abo'},
        {id: '3', name: 'Jaks'},
        {id: '4', name: 'Nurik'},
        {id: '5', name: 'Amir'},
    ],

    messagesData: [
        {id: 1, messages: 'Hey'},
        {id: 2, messages: 'Bro'},
        {id: 3, messages: 'How do you do'},
        {id: 4, messages: 'I am fourth'}
    ],
    newMessageText: ['message']
}

const dialogsReducer = (state = initialState,action) => {

    switch (action.type) {

        // case ADD_MESSAGE:
        //     let newMessage = {
        //         id: 5,
        //         messages: action.message
        //
        //     };
        //     let stateCopy = {...state};
        //     stateCopy.messagesData = [...state.messagesData];
        //    // stateCopy.newMessageText = action.newMessage;
        //     stateCopy.messagesData.push(newMessage);
        //
        //     return stateCopy;

        case ADD_MESSAGE:
            let newMessage = action.message
           return {
                ...state,
               messagesData: [...state.messagesData,{id: 6, messages:newMessage}]
           }


        case UPDATE_MESSAGE: {
            let stateCopy = {...state};
            stateCopy.messagesData = [...state.messagesData];
            stateCopy.newMessageText = action.newMessage;
            return stateCopy;

        }

        default:
            return state;

    }
}

export default dialogsReducer;