import React from "react";
import Header from "./Header";
import * as axios from "axios";
import {connect} from "react-redux";
import {getUserAuth, getUserLogin, getUserLogout, setAuthUserData} from "../../auth-reduce";
import {headersAPI} from "../../api/api";


class HeaderContainer extends React.Component {
    componentDidMount() {
       // this.props.getUserAuth();
        this.props.getUserLogout();
    }

    render() {
        return <Header {...this.props} />
    }
}
const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    login: state.auth.login,
    email: state.auth.email,
    // isLogin: state.auth.isAuth,
    // password: state.auth.password
});
export default connect(mapStateToProps,{setAuthUserData, getUserAuth, getUserLogin,getUserLogout})(HeaderContainer);