import {usersAPI} from "./api/api";

export let followSuccess = (userId) => ({type: 'FOLLOW', userId})
export let unfollowSuccess = (userId) => ({type: 'UNFOLLOW', userId})
export let setUsers = (users) => ({type: 'SET_USERS', users})
export let setCurrentPage = (currentPage) => ({type: 'SET_CURRENT_PAGE', currentPage})
export let setTotalUsersCount = (totalUsersCount) => ({type: 'SET_TOTAL_USERS_COUNT', totalUsersCount})
export let checkIsFetching = (isFetching) => ({type: 'CHECK_IS_FETCHING', isFetching})
export let toggleOnFollowingProgress = (isFetching, userId) => ({type: 'TOGGLE_IS_FOLLOWING_PROGRESS', isFetching, userId})

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const CHECK_IS_FETCHING = 'CHECK_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';

let initialState = {
    users: [],
    pageSize: 5,
    totalCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW: {
            return  {
                ...state,
                users: state.users.map((u) => {
                    if (u.id === action.userId) {
                        return {...u, followed: true}
                    }
                    return u;
                }),

            }
        }

        case UNFOLLOW: {
            return  {
                ...state,
                users: state.users.map((u) => {
                    if (u.id === action.userId) {
                        return {...u, followed: false}
                    }
                    return u;
                }),

            }
        }

        case SET_USERS: {
            return { ...state, users: action.users}
        }

        case SET_CURRENT_PAGE: {
            return { ...state, currentPage: action.currentPage}
        }
        case SET_TOTAL_USERS_COUNT: {
            return { ...state, totalCount: action.totalUsersCount}
        }
        case CHECK_IS_FETCHING: {
            return { ...state, isFetching: action.isFetching}
        }
        case TOGGLE_IS_FOLLOWING_PROGRESS: {
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id=>id != action.userId)
            }
        }

        default:
            return state;

    }
}

export const requestOfUsers = (currentPage, pageSize) =>
    (dispatch) => {
    dispatch(checkIsFetching(true));
    usersAPI.requestUsers(currentPage,pageSize).then(data => {
        dispatch(checkIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
    });

}

export const follow = (userId) =>
    (dispatch) => {
        dispatch(toggleOnFollowingProgress(true, userId));
        usersAPI.usersFollow(userId)
            .then(data => {
                if(data.resultCode==0) {
                    dispatch(followSuccess(userId));
                }
                dispatch(toggleOnFollowingProgress(false, userId));

            });

    }

export const unfollow = (userId) =>
    (dispatch) => {
        dispatch(toggleOnFollowingProgress(true, userId));
        usersAPI.usersFollow(userId)
            .then(data => {
                if(data.resultCode==0) {
                    dispatch(unfollowSuccess(userId));
                }
                dispatch(toggleOnFollowingProgress(false, userId));

            });

    }

export default usersReducer;