import React from "react";
import style from './MyPosts.module.css';
import Posts from "./Posts/Posts";
import {addPostToDataActionCreator, updatePostTextActionCreator} from "../../../profile-reduce";
import {Field, reduxForm} from "redux-form";
import handleSubmit from "redux-form/lib/handleSubmit";
import {maxLengthCreator, required} from "../../../validator/validator";
import {Textarea} from "../../../common/Preloader/FormsControl/FormsControl";


const MyPosts = (props) => {


    let textOfTextarea = React.createRef();

    let addPost = (newPostValue) => {
        let text = textOfTextarea.current.value;
        //props.dispatch(addPostToDataActionCreator());
        props.addPost(newPostValue)

    }
    let onPostChange = () => {
        let text = textOfTextarea.current.value;
        //props.dispatch(updatePostTextActionCreator(text));
        props.updatePostText(text);
    }



    let myPosts = props.myPostsData.map(myPosts => {
        return (
            <Posts message={myPosts.message} likecount={myPosts.likecount}/>
        )
    })
    const onSubmit = (formData) => {
        debugger;
        props.addPost(formData.newPostText);
    }
    return (
    <div>
        <div className={style.postsBlock}>
            <h2>My Posts</h2>
            <div>
                <NewPostReduxForm onSubmit = {onSubmit}/>
            </div>
            {myPosts}


        </div>
    </div>


    )
}
const maxLength10 = maxLengthCreator(10);
const NewPostForm = (props) => {

    return (
        <div>
            <form onSubmit={props.handleSubmit}>
                <Field name="newPostText" component={Textarea} placeholder="New post text..."
                validate={[required,maxLength10]}/>
                <div>
                    <button>Add</button>
                </div>
            </form>
        </div>
    )

}

const NewPostReduxForm = reduxForm({
    form:"newPost"
})(NewPostForm);



export default MyPosts;