import React from 'react'
import style from './Dialogs.module.css'
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";

import {Redirect} from "react-router";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../../common/Preloader/FormsControl/FormsControl";
import {maxLengthCreator, required} from "../../validator/validator";





const Dialogs = (props) => {
    let messageOfArea = React.createRef();
    let addNewMessage = (message) => {

        props.addMessage(message);
    }

    let onMessageChange = (formData) => {
       // let text = messageOfArea.current.value;
        props.MessageChange(formData.message);
    }

    let dialogs = props.dialogsData.map((dialogs) => {
        return (
            <DialogItem name={dialogs.name} id={dialogs.id}/>
        )
    })

    let messages = props.messagesData.map((messages => {
        return (
            <Message message={messages.messages}/>
        )
    }))

    if (!props.isAuth) return <Redirect to={'/login'}/>

    const onSubmit = (formData) => {

        props.addMessage(formData.message);
        //alert(formData.message);
    }
    return (
        <div className={style.dialogs}>
                <div className={style.dialogItems}>
                    {dialogs}
                </div>

                <div className={style.messages}>
                    {messages}
                </div>
            <MessageReduxForm onSubmit={onSubmit}/>

            </div>


    )



}
 const maxLength50 = maxLengthCreator(50)
const MessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field name="message" component={Textarea} placeholder="New message..."
            validate={[required,maxLength50]}/>
            <div>
                <button>Add</button>
            </div>

        </form>

    )
}


const MessageReduxForm = reduxForm({
    form: 'newMessage'
})(MessageForm);



export default Dialogs ;