import React from 'react';
import {Field, reduxForm} from "redux-form";
import style from "./../../common/Preloader/FormsControl/FormsControl.module.css"
import {connect} from "react-redux";
import {getUserLogin} from "../../auth-reduce";

import {Redirect} from "react-router";
import {Input} from "../../common/Preloader/FormsControl/FormsControl";
import {maxLengthCreator, required} from "../../validator/validator";

const maxLength30 = maxLengthCreator(30)
const LoginForm = (props) => {

    return (<div>

            <form onSubmit={props.handleSubmit}>
                <div>

                    <Field name="email" component={Input} type="text" placeholder="email"
                           validate={[required, maxLength30]}/>
                </div>
                <div>

                    <Field name="password" component={Input} type="text" placeholder="password"
                           validate={[required, maxLength30]}/>
                </div>
                <div>

                    <Field name="rememberMe" component={Input} type="checkbox"
                           validate={[required, maxLength30]}/> remember me
                </div>
                {props.error&&<div className={style.formSummaryError}>
                    {props.error}
                </div>}
                <button type="submit">Login</button>
            </form>


        </div>
    )

}
const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm)

const Login = (props) => {
    const onSubmit = (formData) => {
        props.getUserLogin(formData.email, formData.password, formData.rememberMe);
    }
    if (props.isAuth) {
        return <Redirect to='/profile'/>
    }
    return (<div>
            <h1>Login</h1>
            <LoginReduxForm onSubmit={onSubmit}/>

        </div>
    )

}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})
export default connect(mapStateToProps, {getUserLogin})(Login);